body {
  color: #fff;
  background-color: #333;
  font-family: Lato, sans-serif;
  font-size: 18px;
}

.container {
  height: 60px;
  background: #444;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-form {
  width: 450px;
  position: relative;
}

.input {
  height: 45px;
  width: 450px;
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  z-index: 1;
  background: #fff;
  border: 0;
  border-radius: .7rem;
  outline: 0;
  padding: 0 1.6rem;
  font-family: inherit;
  font-size: 24px;
  transition: all .3s cubic-bezier(0, 0, .43, 1.49);
  position: relative;
}

.input:not(:placeholder-shown) {
  width: calc(100% - 150px);
  border-radius: 11px 0 0 11px;
}

.button {
  width: 150px;
  height: 45px;
  color: #fff;
  cursor: pointer;
  background: #000;
  border: 0;
  border-radius: 0 11px 11px 0;
  font-size: 20px;
  display: block;
  position: absolute;
  top: 0;
  right: 0;
}

.load-more {
  cursor: pointer;
  width: 150px;
  height: 50px;
  color: #fff;
  background-color: #444;
  border: 0;
  border-radius: 11px;
  margin-left: auto;
  margin-right: auto;
  font-size: 16px;
  display: block;
}

.load-more:hover, .load-more:focus {
  background-color: #555;
}

.is-hidden {
  display: none;
}

.gallery {
  grid-gap: 20px;
  max-width: calc(100vw - 80px);
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  margin: 50px auto 30px;
  padding: 0;
  list-style: none;
  display: grid;
}

img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  display: block;
}

a {
  color: #fff;
  text-decoration: none;
}

.info {
  text-align: center;
  justify-content: space-around;
  display: flex;
}

.info-item {
  font-size: 14px;
}

span {
  margin-right: 3px;
  font-weight: normal;
}

